import { ThemeContext } from "providers/ThemeProvider";
import React, { useContext, useState } from "react";
import AwesomeSlider from "react-awesome-slider";
import "react-awesome-slider/dist/styles.css";
import {
  Thumbnail,
  WorksWrapper,
  Wrapper,
  Details,
  ReverseWrapper,
  Tab,
} from "./styles";
import { Container } from "../../common/Container";
import ReactPlayer from "react-player/lazy";

import sentiment_clf from "../../../assets/works/ai/sentiment_clf.gif";
import gan from "../../../assets/works/ai/gan.png";
import ml_emotion from "../../../assets/works/ai/ml_emotion.png";
import ballot from "../../../assets/works/ai/ballot.png";

export const Works = () => {
  const { theme } = useContext(ThemeContext);
  const [tab, setTab] = useState("SE");

  function importAll(r) {
    return r.keys().map(r);
  }

  const xceltoken = importAll(
    require.context(
      "../../../assets/works/xceltoken",
      false,
      /\.(png|jpe?g|svg)$/
    )
  );

  const namastenepal = importAll(
    require.context(
      "../../../assets/works/namastenepal",
      false,
      /\.(png|jpe?g|svg)$/
    )
  );

  const flyeasy = importAll(
    require.context(
      "../../../assets/works/flyeasy",
      false,
      /\.(png|jpe?g|svg)$/
    )
  );

  const foodmyway = importAll(
    require.context(
      "../../../assets/works/foodmyway",
      false,
      /\.(png|jpe?g|svg)$/
    )
  );

  const handleActiveTab = (name) => {
    setTab(name);
  };

  const renderWork = () => {
    if (tab === "SE") {
      return (
        <>
          <WorksWrapper>
            <Thumbnail>
              <AwesomeSlider>
                {xceltoken?.map((img, idx) => (
                  <div key={idx}>
                    <img src={img} alt="" />
                  </div>
                ))}
              </AwesomeSlider>
            </Thumbnail>
            <Details theme={theme}>
              <h3>
                <a href="https://www.xceltoken.com/">XcelToken Exchange</a>
              </h3>

              <p>
                XcelToken Exchange is the Easiest Way to Buy and Sell Digital
                Currency. It is a Secure, Trusted and Transparent Global Crypto
                Trading Platform.
              </p>
              <code>ReactJS + Redux</code>
              <code>Trading View</code>
            </Details>
          </WorksWrapper>
          <hr className="mt-2" />
          <ReverseWrapper>
            <Details theme={theme}>
              <h3>NamasteNepal</h3>

              <p>
                NamastNepal is a Social Networking Platform for all type of
                users, where they can Register/Login to their accounts with a
                phone number or the email verification process and subscribe for
                different communities of their choice.
              </p>
              <code>DRF</code>
              <code>ReactJS + Redux</code>
              <code>Websocket</code>
              <code>Django Channels</code>
            </Details>

            <Thumbnail>
              <AwesomeSlider>
                {namastenepal?.map((img, idx) => (
                  <div key={idx}>
                    <img src={img} alt="" />
                  </div>
                ))}
              </AwesomeSlider>
            </Thumbnail>
          </ReverseWrapper>

          <hr className="mt-2" />
          <WorksWrapper>
            <Thumbnail>
              <AwesomeSlider>
                {foodmyway?.map((img, idx) => (
                  <div key={idx}>
                    <img src={img} alt="" />
                  </div>
                ))}
              </AwesomeSlider>
            </Thumbnail>
            <Details theme={theme}>
              <h3>
                {" "}
                <a href="http://food-myway.herokuapp.com/">Foodmyway</a>
              </h3>

              <p>
                Foodmyway is an Automated Meal planning app that lets you plan
                your daily meals with the right amount of ingredients you want
                with suggestions.{" "}
              </p>
              <code>DRF</code>
              <code>ReactJS + Redux</code>
              <code>GraphQL</code>
            </Details>
          </WorksWrapper>

          <hr className="mt-2" />
          <ReverseWrapper>
            <Details theme={theme}>
              <h3>
                <a href="http://fly-easy.herokuapp.com/">Flyeasy</a>
              </h3>

              <p>
                Fly easy is a small Flight booking system where you can have a
                simulated environment of booking flight seats and reserving
                flight tickets.
              </p>
              <code>NodeJS</code>
              <code>ReactJS + Redux</code>
            </Details>
            <Thumbnail>
              <AwesomeSlider>
                {flyeasy?.map((img, idx) => (
                  <div key={idx}>
                    <img src={img} alt="" />
                  </div>
                ))}
              </AwesomeSlider>
            </Thumbnail>
          </ReverseWrapper>
        </>
      );
    } else {
      return (
        <>
          <WorksWrapper>
            <Thumbnail>
              <img src={sentiment_clf} alt="" />
            </Thumbnail>
            <Details theme={theme}>
              <h3>
                <a href="https://github.com/Anyesh/sentiment_classifier">
                  Sentiment Classifier
                </a>
              </h3>

              <p>
                Developed a Sentiment Classifier by fine-tuning BERT Model. The
                models uses bi-directional LSTM with pre-trained weights of
                BERT. It was trained on Tesla V100 GPU on 1.5M tweets data
                labeled as positive and negative. For web application, Flask is
                used.
              </p>
              <code>Python</code>
              <code>Flask</code>
              <code>BERT</code>
            </Details>
          </WorksWrapper>
          <hr className="mt-2" />
          <ReverseWrapper>
            <Details theme={theme}>
              <h3>GAN</h3>

              <p>
                Implemented DCGAN and trained it on Cubism style of Modern Art
                on Tesla V100 GPU to generate similar modern arts.
              </p>
              <code>GAN</code>
              <code>Python</code>
            </Details>

            <Thumbnail>
              <img src={gan} alt="" />
            </Thumbnail>
          </ReverseWrapper>
          <hr className="mt-2" />
          <WorksWrapper>
            <Thumbnail>
              <img src={ml_emotion} alt="" />
            </Thumbnail>
            <Details theme={theme}>
              <h3>
                <a href="https://github.com/Anyesh/fusemachines_training">
                  Emotion Detection and Recognition from Text data
                </a>
              </h3>

              <p>
                NaiveBayes model for Emotion detection and recognition from text
                data. Flask is used for web app and docker for containerized
                app.
              </p>
              <code>Python</code>
              <code>Flask</code>
              <code>MongoDB</code>
              <code>Docker</code>
            </Details>
          </WorksWrapper>
          <hr className="mt-2" />

          <ReverseWrapper>
            <Details theme={theme}>
              <h3>Ballot Paper Classification - Computer Vision Project</h3>

              <p>
                CV model trained on ResNet50 architecture for ballot paper
                classification. Flask is used for web app.
              </p>
              <code>CV</code>
              <code>Flask</code>
              <code>MongoDB</code>

              <code>ResNet50</code>
              <code>Python</code>
            </Details>

            <Thumbnail>
              <img src={ballot} alt="" />
            </Thumbnail>
          </ReverseWrapper>

          <WorksWrapper>
            <Thumbnail>
              <div className="player-wrapper">
                <ReactPlayer
                  url={
                    "https://drive.google.com/file/d/1bD9JSql5QdgsBHyZh0RxG06FGlo0MHZE/preview"
                  }
                  controls={true}
                  playing={false}
                  loop={true}
                  light={true}
                  muted={true}
                  width="100%"
                  height="100%"
                  className="react-player"
                />
              </div>
            </Thumbnail>
            <Details theme={theme}>
              <h3>
                <a href="https://m.me/helamburm">FB Messenger Bot: Gaupalika</a>
              </h3>

              <p>
                Service built on Facebook Messenger Platform for Village
                Development Committee to automate Inquiries and to provide news,
                notices about the village.
              </p>
              <code>Python</code>
              <code>Graph API</code>
              <code>Facebook Messenger</code>
            </Details>
          </WorksWrapper>
          <hr className="mt-2" />
          <ReverseWrapper>
            <Details theme={theme}>
              <h3>
                <a href="https://m.me/435070913709635:">
                  FB Messenger Bot: Trek bot
                </a>
              </h3>

              <p>
                Service built on Facebook Messenger Platform for Travel agencies
                to automate inquiries and to provide news, notices and book the
                new travel packages.
              </p>
              <code>Python</code>
              <code>Graph API</code>
              <code>Facebook Messenger</code>
            </Details>
            <Thumbnail>
              <div className="player-wrapper">
                <ReactPlayer
                  url={
                    "https://drive.google.com/file/d/1-CyAyjQsjvpQjGZh1XH2NGVG9ixYlFQT/preview"
                  }
                  controls={true}
                  playing={false}
                  loop={true}
                  light={true}
                  muted={true}
                  width="100%"
                  height="100%"
                  className="react-player"
                />
              </div>
            </Thumbnail>
          </ReverseWrapper>
        </>
      );
    }
  };

  return (
    <Wrapper id="works" as={Container}>
      <h1>Some Things I've Built</h1>
      <Tab theme={theme}>
        <div className="col">
          <button
            className={tab === "SE" ? "active" : ""}
            onClick={() => handleActiveTab("SE")}
          >
            Software Engineering
          </button>
        </div>
        <div className="col">
          <button
            onClick={() => handleActiveTab("ML")}
            className={tab === "ML" ? "active" : ""}
          >
            Artificial Intelligence
          </button>
        </div>
      </Tab>

      {renderWork()}
    </Wrapper>
  );
};
